.admin_prod_right{
    min-width: 40rem;
    padding: 1rem 5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.admin_prod_right h1{
    font-size: 1.8rem;
    font-weight: 600;
}

.admin_prod_cont{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
}

.prod-form{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.prod-form h5{
    text-transform: capitalize;
    font-weight: 500;
    font-size: 1rem;
}

.photo-btn{
    background-color: #464646;
    color: white;
    padding: 1rem;
    font-size: 1rem;
    border-radius: 9px;
    border: none;
    font-weight: 400;
}

.prod-form input{
    padding: 1rem;
    border-radius: 9px;
    border: 1px solid #b4b4b4;
    font-weight: 500;
    font-size: 1rem;
    color: #000;
}

.prod-form-select{
    border-radius: 9px;
    border: 1px solid #b4b4b4;
}

.prod-form-btn{
    text-transform: capitalize;
    border-radius: 9px;
    border: none;
    padding: 1rem 0;
    font-weight: 600;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    cursor: pointer;
    background-color: #ffbe00;
}

.delete{
    background-color: crimson;
}

@media (max-width: 640px) {
    .admin_prod_right{
        min-width: 100%;
        padding: 1rem;
    }
}